* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.navbar {
    height: 80px;
    width: 100%;
    background-color: aqua;
}

.navbar h1 {
    display: inline;
    line-height: 80px;
    padding-left: 100px;
}

.navLists {
    float: right;
    line-height: 80px;
    padding-right: 40px;
}

.navLists li {
    display: inline-block;
    padding-right: 60px;
}

.navLists li a {
    text-decoration: none;
}

.navLists li button {
    padding: 5px;
    background-color: blue;
    color: white;
    font-weight: bold;
    border: 0px;
    border-radius: 5px;
}

.navLists li a:hover {
    color: #333399;
    cursor: pointer;
}

.navLists li button:hover {
    background-color: #0066ff;
    cursor: pointer;
}


/* //////////////// */

.intro {
    display: flex;
    flex-wrap: nowrap;
    margin: 40px;
}

.intro div {
    width: 50%;
}

.intro .left {
    /* background-color: red; */
    text-align: center;
    font-size: 30px;
    padding: 100px;
}

.intro .right {
    /* background-color: blue; */
    font-size: 20px;
    padding: 100px;
}

.intro .left h1 {
    font-size: 60px;
}

.intro .right textarea {
    resize: none;
    width: 100%;
    height: 250px;
    font-size: large;
    margin-top: 10px;
    padding: 5px;
}

.intro .right textarea::placeholder {
    font-size: large;
}
.intro .right button {
    padding: 5px 10px 5px 10px;
    background-color: #195682;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
}

.intro .right button:hover {
    cursor: pointer;
    background-color: #16476a;
}

/* ////////////////// */


.jobsListContainer {
    width: 100%;
    height: 500px;
    /* background-color: red; */
    padding: 80px;
}

.jobsListContainer ul {
    display: flex;
    list-style: none;
    padding-top: 15px;
}

.jobsListContainer ul li {
    width: 25%;
    display: inline-block;
    padding: 10px;
    text-align: justify;
    border: 1px solid black;
    margin: 5px;
    border-radius: 5px;
}

.jobsListContainer a {
    text-decoration: none;
    float: right;
    cursor: pointer;
}

/* //////////// */

footer {
    width: 100%;
    background-color: grey;
    padding: 80px;
}